.project .header {
  display: flex;
}

.project .header h2 {
  margin: 0 0;
}

.project .list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  margin-top: 0;
}

.project .list li {
  background-color: rgb(230, 230, 230);
  margin: 5px 0;
}

.project .list li h3 {
  margin: 0px;
}

.project .list li p {
  margin: 0px;
}

.project .list li h3 a {
  margin: 0px;
  color: inherit;
  text-decoration: none;
}