.blog .header {
  display: flex;
}

.blog .header h2 {
  margin: 0 0;
}

.blog .header p {
  margin: 0 0;
  margin-left: auto;
}

.blog .list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  margin-top: 0;
}

.blog .list li {
  background-color: rgb(230, 230, 230);
  margin: 5px 0;
}

.blog .list li h3 {
  margin: 0px;
}

.blog .list li p {
  margin: 0px;
}